
import { forEach } from '@angular/router/src/utils/collection';
import { UserService } from './../user.service';
import { User } from './../user';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css', './../../animate.css']
})
export class LoginComponent implements OnInit {

  formUser: String;
  formPass: String;

  showLoginError: Boolean = false;
  constructor(private _userService: UserService, private router: Router) { }

  ngOnInit() {

  }

  onLogin() {
    this._userService.onAuthenticate(this.formUser, this.formPass)
      .subscribe(resUserData => {
        if (Object.keys(resUserData).length !== 0) {
          this.router.navigateByUrl('/meetings');
          this._userService.setCurrentUser(resUserData[0]);
        }
        else {
          this.showLoginError = true;
        }
      });
  }
}



