import { Router } from '@angular/router';
import { UserService } from './../user.service';
import { MeetingService } from './../meeting.service';
import { Meeting } from './../meeting';
import { Component, OnInit } from '@angular/core';
import { User } from './../user';

@Component({
  selector: 'app-meetings-page',
  templateUrl: './meetings-page.component.html',
  styleUrls: ['./meetings-page.component.css'],
  providers: [MeetingService]
})
export class MeetingsPageComponent implements OnInit {

  meetings: Array<Meeting>;
  isProf: Boolean = true;
  isShowLastWeek: Boolean = false;
  isStyleLastWeek: Boolean = false;
  selectedMeeting: Meeting;
  lastMeetingDate: Date;
  startMeetingDate: Date;
  currentUser: User;
  constructor(private _meetingService: MeetingService, private _userService: UserService,
    private router: Router) { }
  ngOnInit() {
    this._meetingService.getMeetings()
      .subscribe(resMeetingData => { this.meetings = resMeetingData; this.setDatesRange(); });
    this.currentUser = this._userService.getCurrentUser();
    if (this.currentUser === undefined) {
      this.router.navigateByUrl('/login');
    }
    if (this.currentUser.UserTypeID === '1') {
      this.isProf = true;
    }
    else {
      this.isProf = false;
    }
  }
  onEventAddMeeting(meeting: Meeting) {
    this._meetingService.addMeeting(meeting)
      .subscribe(resNewMeeting => {
        this.meetings.push(resNewMeeting);
        this.setDatesRange();
      });
  }
  onSelectMeeting(meeting: any) {
    this.selectedMeeting = meeting;
  }
  onUpdateMeetingEvent(meeting: any) {
    this._meetingService.updateMeeting(meeting)
      .subscribe(resUpdatedMeeting => {
        this.selectedMeeting = resUpdatedMeeting;
        this.setDatesRange();
      });
  }
  onDeleteMeetingEvent(meeting: any) {
    const meetingArray = this.meetings;
    this._meetingService.deleteMeeting(meeting)
      .subscribe(resDeletedMeeting => {
        for (let i = 0; i < meetingArray.length; i++) {
          if (meetingArray[i]._id === meeting._id) {
            meetingArray.splice(i, 1);
          }
        }
        this.setDatesRange();
      });
    this.selectedMeeting = null;
  }
  getDateFromMeeting(meeting: Meeting) {
    return new Date(meeting.MeetingDate.substring(0, 11));
  }
  setDatesRange() {
    this.lastMeetingDate = this.getDateFromMeeting(this.meetings[0]);
    for (const meeting of this.meetings) {
      const tmp = this.getDateFromMeeting(meeting);
      if (tmp > this.lastMeetingDate) {
        this.lastMeetingDate = tmp;
      }
    }
    const temp = this.lastMeetingDate;
    temp.setDate(temp.getDate() - 7);
    this.startMeetingDate = temp;
  }

  toggleShowLastWeek(status: boolean) {
    if (status) {
      this.isShowLastWeek = true;
    }
    else {
      this.isShowLastWeek = false;
    }
  }
  toggleStyleLastWeek() {
    this.isStyleLastWeek = !this.isStyleLastWeek;
  }
}



