import { Meeting } from './../meeting';
import { Component, OnInit, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-meeting-controls',
  templateUrl: './meeting-controls.component.html',
  styleUrls: ['./meeting-controls.component.css', './../../animate.css'],
  outputs: ['addMeeting','showLastWeekEvent','toggleStyleLastWeekEvent'],
  inputs: ['isProf']
})
export class MeetingControlsComponent implements OnInit {
  hidenewMeeting: Boolean = true;
  isProf: boolean;
  styleLastWeek: Boolean = false; // for changing buttons in style last week
  public addMeeting = new EventEmitter();
  public showLastWeekEvent = new EventEmitter();
  public toggleStyleLastWeekEvent = new EventEmitter();
  constructor() { }

  ngOnInit() {
  }
  onAddNewMeeting(meeting: any) {
    this.addMeeting.emit(meeting);
    this.hidenewMeeting = true;
  }
  toggleNewMeeting() {
    this.hidenewMeeting = !this.hidenewMeeting;
  }
  onShowLastWeekEvent(status: boolean)
  {
    this.showLastWeekEvent.emit(status);
  }
  onToggleStyleLastWeekEvent()
  {
  this.toggleStyleLastWeekEvent.emit();
  this.styleLastWeek = !this.styleLastWeek;
  }
}



