
import { Meeting } from './../meeting';
import { Component, OnInit, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-meeting-detail',
  templateUrl: './meeting-detail.component.html',
  styleUrls: ['./meeting-detail.component.css'],
  inputs: ['meetings', 'isProf', 'isShowLastWeek', 'startMeetingDate', 'isStyleLastWeek'],
  outputs: ['updateMeetingEvent', 'deleteMeetingEvent']
})
export class MeetingDetailComponent implements OnInit {
  meetings: any;
  selectedMeeting: Meeting;
  isProf: any;
  isShowLastWeek: boolean;
  isStyleLastWeek: boolean;
  startMeetingDate: Date;
  public editId: Boolean = false;
  public editDesc: Boolean = false;
  public editDate: Boolean = false;
  private updateMeetingEvent = new EventEmitter;
  private deleteMeetingEvent = new EventEmitter;
  constructor() { }
  ngOnInit() {

  }
  onIdClick() {
    if (this.isProf) {
      this.editId = !this.editId;
    }
  }
  onDescClick() {
    if (this.isProf) {
      this.editDesc = !this.editDesc;
    }
  }
  onDateClick() {
    if (this.isProf) {
      this.editDate = !this.editDate;
    }
  }

  ngOnChanges() {
    this.editOff();
  }

  editOff() {
    this.editId = false;
    this.editDesc = false;
    this.editDate = false;
  }

  toggleMeetingStatus(meeting: Meeting) {
    if (meeting.MeetingStatusID === '1') {
      meeting.MeetingStatusID = '0';
    }
    else {
      meeting.MeetingStatusID = '1';
    }
    this.onUpdate(meeting);

  }
  onUpdate(meeting: any) {
    this.updateMeetingEvent.emit(meeting);
    this.editOff();
  }
  onDelete(meeting: Meeting) {
    this.deleteMeetingEvent.emit(meeting);
  }
  onSelect(meeting: Meeting) {
    this.selectedMeeting = meeting;
  }

  getDateFromMeeting(meeting: Meeting) {
    return new Date(meeting.MeetingDate.substring(0, 11));
  }
  checkIfDateInRange(meeting: Meeting) {
    if (this.getDateFromMeeting(meeting) >= this.startMeetingDate) {
      return true;
    }
    return false;
  }
}



