import { User } from './user';
import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class UserService {

  private _getUrl = 'api/users';
  private _postUrl = 'api/users';
  private _putUrl = 'api/users/';  // because of /:id
  private _deleteUrl = 'api/users/';
  private connectedUser: User;
  constructor(private _http: Http) { }

  getUsers() {
    return this._http.get(this._getUrl)
      .map((response: Response) => response.json());
  }

  setCurrentUser(user: User) {
    this.connectedUser = user;
  }

  getCurrentUser() {
    return this.connectedUser;
  }

  onAuthenticate(user: String, pass: String) {
    return this._http.get(this._getUrl + '/' + user + '/' + pass)
      .map((response: Response) => response.json());
  }
}



